
.ScreenContainer {
    height: 100vh;
    background: transparent;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.IntroContainer {
    width: 80%;
    background: #FB6107;
    border-radius: 25px;
    color: black;
    font-weight: 800;
    padding: 25px;
    margin-top: 10%;
    border: 5px solid black;
    font-size: 18px;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.IntroContainer h2 {
    background: #3A86FF;
    padding: 3px;
    border: 3px solid black;
    border-radius: 25px;
    font-size: 24px;
}

.tujuan {
    background: white;
    border: 5px solid black;
    padding: 5px;
    font-size: 15px;
    text-align: justify;
}

.IntroContainer span {
    text-transform: capitalize;
}

.ArrowContainer {
    background: transparent;
    width: 80%;
    display: flex;
    margin-top: 15px;
    justify-content: space-between;
}

.ArrowContainer img {
    background: transparent;
    width: 10vh
    ;
}

.ArrowContainer Link {
    background: transparent;
}

