.bgContainer {
    width: 100%;
    background: #F3DE2C;
    z-index: -999;
    position: fixed;
    min-height: 100%;
}

.ornamen1 {
    width: 60vh;
    margin-right: 350px;
    position: absolute;
    top: -200px;
    left: -200px;
    -webkit-animation: spin 15s linear infinite;
    -moz-animation:  spin 15s linear infinite;
    animation: spin 15s linear infinite;
}

@-webkit-keyframes spin {
    100%{-webkit-transform: rotate(360deg);}
}

@-moz-keyframes spin {
    100%{-webkit-transform: rotate(360deg);}
}

@keyframes spin {
    100%{-webkit-transform: rotate(360deg);}

}

.ornamen2 {
    width: 70vh;
    bottom: 0;
    right: 0;
    margin-left: 150px;
    
    margin-top: 350px;
    -webkit-animation: spin 15s linear infinite;
    -moz-animation:  spin 15s linear infinite;
    animation: spin 15s linear infinite;
}

